.item {
    display: flex;
    gap: 10px;
    align-items: center;
    padding: 5px 10px;
    border: 2px solid #000000;
}

.status {
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
}

.avatar {
    border-radius: 5px;
}

.name {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.3;
}